<template>
  <div class="page-card">
    <a
      :href="url"
      class="page-card__title"
      v-text="title"
    />
    <a
      :href="url"
      class="page-card__link text-link"
    >
      <span v-text="$string('page.read_more')" />
      <icon-arrow-up-right />
    </a>
  </div>
</template>

<script>

/**
 * Vue: Page card (page-card)
 * -----------------------------------------------------------------------------
 * Page Card to render pages returned by Algolia.
 *
 */
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'

export default {
  name: 'PageCard',

  components: {
    IconArrowUpRight,
  },

  props: {
    title: {
      type: String,
      default: '',
    },
    handle: {
      type: String,
      default: '',
    },
  },

  data() {
    return {

    }
  },

  computed: {

    /**
     * Compute page URL
     */
    url() {
      return `${cnvs.routes.root}pages/${this.handle}`
    },
  },
}
</script>

